import React from 'react';
import {connect} from 'react-redux';
import vkconnect from '@vkontakte/vkui-connect';
import {closePopout, goBack, openModal, openPopout, setPage} from '../../store/router/actions';
import {Div, Panel, Alert, Group, Button, Cell, Tabs, CellButton, FormLayout, Input, Select, Search, List, PanelHeader} from "@vkontakte/vkui";
import Icon24Write from '@vkontakte/icons/dist/24/write';
import {setActiveTab, setScrollPositionByID} from "../../store/vk/actions";
import PanelHeaderBack from '@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack';
import Icon24MoreVertical from '@vkontakte/icons/dist/24/more_vertical';
const qs = require('querystring');
var params = window.location.search.replace('?', '').replace('%2C', ',');

const urlParams = qs.parse(params);
const ordered = {};
Object.keys(urlParams).sort().forEach((key) => {
if (key.slice(0, 3) === 'vk_') {
ordered[key] = urlParams[key];
}
});
class HomePanelBase extends React.Component {
    state = {
        showImg: false,
        value1: "",
        value2: "",
        res: "    .",
        res2: " ",
        valid1: '',
        valid2: '',
        popout: '',
        input1: 'false',
        input2: ''
    };
onChanges = (e) => {
const { name, value } = e.currentTarget;
this.setState({ [name]: value });
}
    showImg = () => {
        this.setState({showImg: true});
    };
openPopout() {
        this.props.openPopout(
            <Alert
                actions={[{
                    title: '',
                    autoclose: true,
                    style: 'cancel',
                }]}
                onClose={() => this.props.closePopout()}
            >
                <h2></h2>
                <p>  , ..       .</p>
            </Alert>
        );
    }
    openPopout2() {
        this.props.openPopout(
            <Alert
                actions={[{
                    title: '',
                    autoclose: true,
                    style: 'cancel',
                }]}
                onClose={() => this.props.closePopout()}
            >
                <h2></h2>
                <p>  , ..    WEB .</p>
            </Alert>
        );
    }
clear = () => {
this.setState({value1:'',value2:''})
}
copy = () => {
if(ordered.vk_platform != 'desktop_web'){
if(this.state.input1 === 'false'){
    this.openPopout();
}
if(this.state.input1 === 'true'){
vkconnect.send("VKWebAppCopyText", {text: this.state.res});
}
}else{
this.openPopout2();
}
}

openras() {
        this.props.openPopout(
            <Alert
                actions={[{
                    title: '',
                    autoclose: true,
                    style: 'cancel',
                    action: this.clear,
                },{
                    title: '',
                    autoclose: true,
                    style: 'cancel',
                    action: this.copy,
                }]}
                onClose={() => this.props.closePopout()}>
                <h2></h2>
                <p>   :</p>
            </Alert>
        );
    }

num = () => {
var result;
var result2;
if(this.state.value1 > 0 & this.state.value1 <= 99999){
if(this.state.value2 >= 1 & this.state.value2 <= 99999){
if(this.state.value1 && this.state.value2){
result = (this.state.value1 * this.state.value2).toFixed(3);
result2 = (result / 1000).toFixed(3);
this.setState({res: "(m) = "  + result+ "  ", input1: 'true'});
this.setState({valid: 'default'});
console.log('TESTIM999');
vkconnect.send("VKWebAppTapticImpactOccurred", {"style": "light"});
}else{
this.setState({res: "  .   .", input1: 'false'});
}
}else{
this.setState({res: "  .   .", input1: 'false'});
}
}else{
this.setState({res: "  .   .", input1: 'false'});
}
console.log(this.state.res);
}


    render() {
        const {id, setPage, withoutEpic} = this.props;
        return (
            <Panel id={id}>
                <PanelHeader left={<PanelHeaderBack onClick={this.props.goBack} />}>
  
</PanelHeader>
<Group title=" ?">
<Div>
<p oncopy="return false;">           "". </p>
</Div>
</Group>



<Group title="">
<Div>
 : <b>m=n*M</b>
</Div>
</Group>

<Group title="">
<Div>



<Input
min="0"
max="99999"
value={this.state.value1}
type="number"
top=" .(n)"
name="value1"
placeholder=" .(n)"
onChange={this.onChanges}
status={this.state.value1 <= 0 || this.state.value1 >= 99999 ? 'error' : 'default'}
onKeyDown={this.func}
/>

<br/>

<Input
min="0"
max="999999999"
value={this.state.value2}
type="number"
top=" .(M)"
name="value2"
placeholder="  (M)"
onChange={this.onChanges}
status={this.state.value2 <= 0 || this.state.value2 >= 99999 ? 'error' : 'default'}
onKeyDown={this.func}
/>

<br/>


<Button size="xl" level="commerce" onClick={(this.num)}></Button>

</Div>
</Group>
<Group title="">
<CellButton onClick={() => this.openras()}>{this.state.res}</CellButton>
</Group>
</Panel>
        );
    }

}

const mapDispatchToProps = {
    setPage,
    goBack,
    openPopout,
    closePopout,
    openModal
};

export default connect(null, mapDispatchToProps)(HomePanelBase);
